    'Schulden',
    'Urteil',
    'Rate',
    'Vergleich',

    'Verjaehrung',
    'Lieferstop',
    'Leistungsstopp',
    'Leistungsstop',
    'Lieferstopp',
    'Mahnung',
    'Mahngebhr',
    'Mahngeb',
    'Limit',
    'berschreitung',
    'Ueberschreitung',
    'Androhung',
    'Drohung',
    'Vorfllig',
    'Vorfaellig',
    'Vorkasse',
    'Proforma',
    'Creditreform',
    'Sumnis',
    'Saeumnis',
    'Anruf',
    'Erinnerung',
    '1.M.',
    '2.M.',
    '3.M.',
    'Letzte',
    'Verzug',
    'Sicherheit',
